import type { UmbPropertyValuePreset, UmbPropertyValuePresetApiCallArgs, UmbPropertyTypePresetModelTypeModel } from '@umbraco-cms/backoffice/property';
import type { UmbPropertyEditorConfig } from '@umbraco-cms/backoffice/property-editor';

export class MyPropertyValuePresetApi implements UmbPropertyValuePreset<string, UmbPropertyEditorConfig> {
	async processValue(value: undefined | string,
		config: UmbPropertyEditorConfig,
		typeArgs: UmbPropertyTypePresetModelTypeModel,
		callArgs: UmbPropertyValuePresetApiCallArgs
	) {
		void config; // to avoid unused variable warning

		var initialValue = "Preset value";

		if (typeArgs?.variesByCulture) {
			initialValue += " varies by culture: " + callArgs?.variantId?.culture;
		}
		if (typeArgs?.variesBySegment) {
			initialValue += " varies by segment: " + (callArgs?.variantId?.segment ?? 'default');
		}
		
		return value ? value : initialValue;
	}

	destroy(): void { }
}

export { MyPropertyValuePresetApi as api };